.uiKitHF{
    background: #F4F4F6;
    width: 1440px;
    padding: 100px 0 130px 0;

    .logoo{
        height: 100px;
    }
    .content{
        background: #fff;
        margin-top: 40px;
        margin-bottom: 40px;

        .bottomMenu{
            padding: 89px 0;
        }

        &.footer_shadow{
            margin-bottom: 0px;
            z-index: 200;
        }
        &:nth-child(4){
            margin-top: -9px;
            box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);
            z-index: 200;
            .footer{
                padding: 9px 0;
            }
        }
    }
    .content_container__footer{
        background: #fff;
        width: 768px;
        margin: 0 auto;

        .footer__column{
            padding: 120px 0 121px 0;

            p{
                margin: 17px 0 22px 0;
            }

            .footer_socBlock_network{
                padding: 0 11px;
                
                &.twitter{
                    margin: -2px 0 0 -2px;
                }
                &.facebook{
                    width: 22px;
                    height: 22px;
                    margin-top: 1px;
                }
                &.instagram{
                    width: 22px;
                    height: 22px;
                    margin-top: 1px -2px 0 0;
                }
            }
        }
    }
}