

.searchRoom{
	margin-top: 30px;
	display: flex;
	justify-content: space-between;
	&.content_container {
		margin: 0 9.6% 0 9.7%;
	}

	.searchRoom_filters{
		width: 266px;
		display: flex;
		flex-direction: column;
		flex-shrink: 0;
		margin-right: 4.6%;//53px;

		h3{
			font-size: 12.4px;
		}

		.blockDropdown{
			width: 100%;
			.blockDropdown_dropdown{
				width: 100%;
			}
		}	

		.searchRoom_filters_date,
		
		.searchRoom_filters_additional,
		.checkboxList{
			margin-top: 30px;
		}
		.searchRoom_filters_guest{
			margin-top: 20px;
		}

		.searchRoom_filters_comfort{
			margin-top: 20px;
			
			.blockDropdown_dropdown__whisItems{
				margin-top: 4px;

				img{
					right: 16.5px;
				}
			}

			.blockDropdown_dropdownItems{
				padding-bottom: 5px;
				box-shadow: 0px 10px 20px rgba(31, 32, 65, 0.05);

				.blockDropdown_dropdownItems_line{
					height: 38px;

					.blockDropdown_dropdownItems_line_incBlock{
						margin: 6px 6px 0 0;
					}

					h3{
						font-size: 12.2px;
					}
					span{
						font-size: 12.2px;
						margin-top: -3px;
					}
				}
			}
		}
		.searchRoom_filters_diapason{
			margin-top: 26px;
		}
		.searchRoom_filters_features{
			margin-top: 25px;
			
			.checkbox{
				margin-top: 16px;

				.checkbox_item_label{
					line-height: 20.5px;
				}
			}
		}
		.searchRoom_filters_availability{
			margin-top: 18px;

			.checkbox_item{
				.p_200{
					width: 200px;
				}
				.p_225{
					width: 225px;
				}
	
				.checkbox_item_label{
					width: 210px;
				}
			}
		}

		.searchRoom_filters_diapason{
			p{
				color: rgba(31, 32, 65, 0.5);
				font-size: 12px;
				margin: 0;
			}

			.searchRoom_filters_diapason_text{
				margin-top: 5px;
			}
		}
		
		.checkboxList{
			h3{
				font-size: 12.2px;
			}
			img{
				right: 17px;
			}
			.checkbox {
				margin-top: 18px;
			}
		}
		
	}

	.searchRoom_rooms{
		display: flex;
		flex-wrap: wrap;
		justify-content: space-between;
		padding-bottom: 70px;
		width: 100%;

		h1{
			width: 100%;
			margin: 29px 0 10px 5px;
			font-size: 25px;
		}

		.searchRoom_rooms_line{
			display: flex;
			justify-content: space-between;
		}

		.searchRoom_rooms_nav{
			width: 100%;
			display: flex;
			justify-content: center;
			margin: 31px 0 10px 0;

			.searchRoom_rooms_nav_num{
				width: 40px;
				height: 40px;
				color: rgba(31, 32, 65, 0.5);
				font-size: 12px;
				font-weight: bold;
				border-radius: 50%;

				display: flex;
				justify-content: center;
				align-items: center;
				cursor: pointer;

				&.searchRoom_rooms_nav_num__active{
					background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
					color: #fff;
				}
				&.searchRoom_rooms_nav_num__next{
					background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
					color: #fff;
					position: relative;

					&:after{
						content: '';
						width: 20px;
						height: 20px;
						cursor: pointer;
						position: absolute;
						top: 11px;
						left: 11px;
						background-repeat: no-repeat;
						background-image: url('/project/img/arrow_white.svg');
					}
				}
			}
		}
		.searchRoom_rooms_nav_label{
			width: 100%;
			display: flex;
			justify-content: center;
		}
	}
}


@media (max-width: 1170px){
	.searchRoom{
		.searchRoom_filters{
			margin-left: 20px;
		}	
		.searchRoom_rooms{
			justify-content: space-around;
			h1{
				text-align: center;
				padding: 0 15px;
			}
		}
	}
}

@media (max-width: 620px){
	.searchRoom{
		flex-wrap: wrap;
		justify-content: center;
		.searchRoom_filters{
			margin: 0;
		}
	}	
}