.pagination{
	width: 100%;
	display: flex;
	justify-content: flex-start;//center;
	margin: 30px 0 10px 0;

	&.__center{
		justify-content: center;
	}

	.pagination_num{
		width: 40px;
		height: 40px;
		color: rgba(31, 32, 65, 0.5);
		font-size: 12px;
		font-weight: bold;
		border-radius: 50%;

		display: flex;
		justify-content: center;
		align-items: center;

		cursor: pointer;

		&.pagination_num__active{
			background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
			color: #fff;
		}
		&.pagination_num__next{
			background: linear-gradient(180deg, #6FCF97 0%, #66D2EA 100%);
			color: #fff;
			position: relative;

			.arrow{
				width: 20px;
				height: 20px;
				cursor: pointer;
				position: absolute;
				top: 12px;
				right: 9px;
				background-repeat: no-repeat;
				&.arrow__white{	
					background-image: url('/project/img/arrow_white.svg');
				}
			}
		}
	}
}
.pagination_label{
	width: 100%;
	display: flex;
	justify-content: flex-start;//center;

	&.__center{
		justify-content: center;
	}
}