//Радиокнопки
.radio{
	vertical-align: center;
	.radio_input{
		margin: auto 10px auto 0;
		width: 20px;
		height: 20px;
		display: none;

		&:checked{
			+ .radio_label{
				&:after{
					content: "";
					background: linear-gradient(180deg, #BC9CFF 0%, #8BA4F9 100%);
					display: inline-block;
					width: 12px;
					height: 12px;
					border-radius: 50%;
					position: absolute;
					left: 4px;
					top: 4px;
				}
			}
		}		
	}

	.radio_label{
		display: inline-block;
		cursor: pointer;
		position: relative;
		padding-left: 25px;
		margin-right: 0;
		line-height: 18px;
		user-select: none;
		margin: 10px 20px 10px 0;

		&:before{
			content: "";
			background-color: #fff;
			display: inline-block;
			width: 20px;
			height: 20px;
			border-radius: 50%;
			border: 1px solid rgba(31, 32, 65, 0.25);
			box-sizing: border-box;
			position: absolute;
			left: 0;		
		}
	}
}